import React from 'react';
import { ReactComponent as Tab } from '../assets/icons/Tab.svg';
import { ReactComponent as Cross } from '../assets/icons/x.svg';
import { ReactComponent as Plus } from '../assets/icons/plus.svg';
import { ReactComponent as Minus } from '../assets/icons/minus.svg';
import { ReactComponent as Maximize } from '../assets/icons/maximize.svg';
import './Project.css'
const Project = ({ heading, data }) => {

    let x = {
        tags: ['js', 'HTML', 'CSS', 'ServiceWorker'],
        description: [
            `An offline to do list`,
            `Works by installing a service worker`,
            `Allows user to create,edit,update and delete entries`,
            `Update the entries in localstorage after every change.`,
            `Allow users to filter out entries based on active/completed status`,
            `Allow users to mark all entries as completed`,
            `Allow users to delete all completed entries`,
        ],
        github: 'http://github.com/rohanakar/todo',
        live: 'https://rohanakar.github.io/todo'
    }

    return (
        <div className='project'>
            <div className='Top' style={{ backgroundColor: data['color'] }}>
                <div className='Tabs'>
                    <div className='title'>
                        {heading}
                    </div>
                    <Tab className='Tab' />
                    <Cross className='cross' />
                    <Plus className='plus' />
                    <div className='icons'>
                        <Minus className='icon' />
                        <Maximize className='icon' />
                        <Cross className='icon' />
                    </div>


                </div>
            </div>

            <div className='project-content'>
                <div className='hidden'>{heading}</div>
                <div className='img'>
                    <img src={'/Screenshot/' + heading + '/heading.jpg'} />
                </div>
            </div>
            <div className='bottom' style={{ backgroundColor: data['color'] }} />
        </div>
    )
}

export default Project;