import React from 'react';
import { GmailIconGroup } from './Navigate';
import SearchFilter from './SearchFilter';
import Project from './Project';
import { useSelector, useDispatch } from 'react-redux';
import Chip from '@material-ui/core/Chip';
import './Projects.css';
const style = {
    outer: {
        backgroundColor: '#EFEFEF',
        color: 'black',
        overflowY: 'scroll',
        position: 'relative',
    },
    title: {
        transform: 'translateZ(2px)',
        padding: '30px',
        pointerEvents: 'none',
        color: 'black',
        fontWeight: 'bold'
    }
}


const FilterList = () => {
    const { tagSelected } = useSelector(state => state.projectReducer);
    const dispatch = useDispatch();

    return (tagSelected.map((val, id) => <Chip
        key={id} label={val} style={{ margin: '10px', backgroundColor: 'white' }}
        onDelete={() => {
            dispatch({
                type: 'setTagSelected',
                payload: val
            })
        }}
    />));
}


const Landing = () => {
    const { projectsContent } = useSelector(state => state.projectReducer);
    return (
        <div className='outer' style={style.outer}>
            <div style={{ position: 'fixed', zIndex: 500 }}>
                <GmailIconGroup />
                <div className='title' style={style.title}>
                    Rishabh Chaudhary
                </div>
            </div>
            <div className='content'>
                <div className='heading'>
                    Self Projects
                </div>
                <div className='subheading'>
                    These are various side projects that I worked upon my free time to explore and
                    improvise various tools and technologies. You can filter by various tags .
                </div>
                <div style={{ position: 'sticky', top: 0, display: 'block', background: '#efefef',zIndex:20 }}>
                    <SearchFilter />
                    <div style={{ display: 'flex', justifyContent: 'center', marginTop: '2em', flexWrap: 'wrap' }}> <FilterList /></div>
                    <br />
                    <hr />
                    <br />
                </div>
                <div className='projects'>
                    {getJsx(projectsContent)}
                </div>
            </div>
        </div>
    );
}

const getJsx = (projectsContent) => {
    let op = [];
    Object.keys(projectsContent).forEach((val) => (
        op.push(<Project key={val} data={projectsContent[val]} heading={val}/>)
    ))
    return op;
}

export default Landing;